export default {
	props: {
		// 
		value: {
			type: [String, Number, Boolean],
			default: ''
		},
		modelValue: {
			type: [String, Number, Boolean],
			default: ''
		},
		// radio
		disabled: {
			type: Boolean,
			default: false
		},
		// circle-square-
		shape: {
			type: String,
			default: 'circle'
		},
		// parentactiveColor
		activeColor: {
			type: String,
			default: '#2979ff'
		},
		// 
		inactiveColor: {
			type: String,
			default: '#c8c9cc'
		},
		// 
		name: {
			type: String,
			default: ''
		},
		// px
		size: {
			type: [String, Number],
			default: 18
		},
		// row-column-
		placement: {
			type: String,
			default: 'row'
		},
		// label
		label: {
			type: [String],
			default: ''
		},
		// label  '#303133' 
		labelColor: {
			type: [String],
			default: '#303133'
		},
		// labelpx
		labelSize: {
			type: [String, Number],
			default: 14
		},
		// checkbox( false )
		labelDisabled: {
			type: Boolean,
			default: false
		},
		// 
		iconColor: {
			type: String,
			default: '#fff'
		},
		// px
		iconSize: {
			type: [String, Number],
			default: 12
		},
		// 
		borderBottom: {
			type: Boolean,
			default: false
		},
		// 
		iconPlacement: {
			type: String,
			default: 'left'
		},
		...uni.$uv?.props?.radioGroup
	}
}