export default {
	props: {
		value: {
			type: [String, Number],
			default: ''
		},
		modelValue: {
			type: [String, Number],
			default: ''
		},
		// 
		// number-app-vueapp-nvue
		// idcard-QQ
		// digit-AppnvueQQ
		// text-
		type: {
			type: String,
			default: 'text'
		},
		// 
		disabled: {
			type: Boolean,
			default: false
		},
		// 
		disabledColor: {
			type: String,
			default: '#f5f7fa'
		},
		// 
		clearable: {
			type: Boolean,
			default: false
		},
		// 
		password: {
			type: Boolean,
			default: false
		},
		//  -1 
		maxlength: {
			type: [String, Number],
			default: -1
		},
		// 	
		placeholder: {
			type: String,
			default: null
		},
		// placeholderstylescoped/deep/
		placeholderClass: {
			type: String,
			default: 'input-placeholder'
		},
		// placeholder
		placeholderStyle: {
			type: [String, Object],
			default: 'color: #c0c4cc'
		},
		// send|search|next|go|doneuni-app
		// https://uniapp.dcloud.io/component/input
		// https://uniapp.dcloud.io/component/textarea
		confirmType: {
			type: String,
			default: 'done'
		},
		// H5
		confirmHold: {
			type: Boolean,
			default: false
		},
		// focus
		holdKeyboard: {
			type: Boolean,
			default: false
		},
		// 
		//  H5 nvue  focus()blur() 
		focus: {
			type: Boolean,
			default: false
		},
		// App3.0.0+
		autoBlur: {
			type: Boolean,
			default: false
		},
		// focus
		cursor: {
			type: [String, Number],
			default: -1
		},
		// 
		cursorSpacing: {
			type: [String, Number],
			default: 30
		},
		// selection-end
		selectionStart: {
			type: [String, Number],
			default: -1
		},
		// selection-start
		selectionEnd: {
			type: [String, Number],
			default: -1
		},
		// 
		adjustPosition: {
			type: Boolean,
			default: true
		},
		// left|center|right
		inputAlign: {
			type: String,
			default: 'left'
		},
		// 
		fontSize: {
			type: [String, Number],
			default: '14px'
		},
		// 
		color: {
			type: String,
			default: '#303133'
		},
		// 
		prefixIcon: {
			type: String,
			default: ''
		},
		// 
		prefixIconStyle: {
			type: [String, Object],
			default: ''
		},
		// 
		suffixIcon: {
			type: String,
			default: ''
		},
		// 
		suffixIconStyle: {
			type: [String, Object],
			default: ''
		},
		// surround-bottom-none-
		border: {
			type: String,
			default: 'surround'
		},
		// disableddisabledreadonly
		readonly: {
			type: Boolean,
			default: false
		},
		// circle-square-
		shape: {
			type: String,
			default: 'square'
		},
		// 
		formatter: {
			type: [Function, null],
			default: null
		},
		// 
		ignoreCompositionEvent: {
			type: Boolean,
			default: true
		},
		...uni.$uv?.props?.input
	}
}